import common from'../../util/common.js'
import code from "../../util/code.js"
import qs from"../../components/qs.js"
export default {
	data() {
		return {
			// 获取验证码
			disabled: false,
			interval: undefined,
			totalCount: 0,
			phone: '',//电话
			code: '',//code
			pwd:'',//密码
			
		}
	},
	onLoad() {
		uni.setStorageSync("pageState","3");
	},
	onShow() {
		uni.setStorageSync("pageState","3")	
	},
	computed: {
		btnText() {
			return this.totalCount !== 0 ? `${this.totalCount}S后获取` : "获取验证码"
		}
	},
	methods: {
		
		telI: function(event) {
			this.phone = event.target.value
			console.log(this.phone)
		},
		codeI: function(event) {
			this.code = event.target.value
			console.log(this.code);
		},
		pwdI: function(event) {
			this.pwd = event.target.value
			console.log(this.pwd);
		},
		getlogin() {
			var telVal = code.validatePhoneNumber(this.phone);
			if (telVal == false) {
				uni.showToast({
					title: "请输入正确的手机号",
					icon: 'none',
					duration: 2000
				})
				return
			} else {
				
				var data =  {
					phone: this.phone
				}
				data = qs.stringify(data);
				uni.request({
					url: common.port.mCode,
					data: data,
					method: "POST",
					dataType: "json",
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						console.log(res.data);
						if(res.data.code == 500){
							clearInterval(this.interval)
							uni.showToast({
								title: res.data.msg,
								icon: "none",
								duration: 2000
							});
						}else{
							uni.showToast({
								title: '验证码发送成功注意查收',
								icon: "none",
								duration: 2000
							});
							this.totalCount = 60;
							this.interval = setInterval(() => {
								this.totalCount--;
								if (this.totalCount === 0) {
									clearInterval(this.interval)
									this.disabled = false
								}
							}, 1000);
						}
					}
				})
				
			}
		},
		submit: function() {
			var telVal = code.validatePhoneNumber(this.phone);
			if (telVal == false) {
				uni.showToast({
					title: "请输入正确的手机号",
					icon: 'none',
					duration: 2000
				})
				return
			} else if (this.code == "") {
				uni.showToast({
					title: "请输入验证码",
					icon: 'none',
					duration: 2000
				})
				return
			}else if (this.pwd == "") {
				uni.showToast({
					title: "请输入密码",
					icon: 'none',
					duration: 2000
				})
				return
			} else {
				var data = {
					phone: this.phone,
					code: this.code,
					password:this.pwd
				}
				var register = qs.stringify(data);
				uni.request({
					url: common.port.mRegister,
					data: register,
					header: {
						'content-type': 'application/x-www-form-urlencoded',
					},
					method: "POST",
					dataType: "json",
					success: (res) => {
						console.log(res.data);
						if (res.data.code == 500) {
							uni.showToast({
								title: result.data.msg,
								icon: 'none',
								duration: 2000
							})
							return
						} else {
							uni.setStorageSync('userIdS', res.data.id);
							if(uni.getStorageSync('authorityS') == 2){
								uni.redirectTo({
									url: '../pwd/pwd'
								})
							}else{
								uni.redirectTo({
									url: '../notice/notice'
								})
							}
						}
					}
				})
			}
		},
		back:function(e){
			uni.reLaunch({
				url:"../login/login"
			})
		},
	}
}
